import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare const $: any;
declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}





export const ROUTES: RouteInfo[] = [
   { path: 'users', title: ' ',  icon:' supervisor_account', class: ' '   },
   { path: 'language', title: '  ',  icon:' language', class: '' },
   { path: 'musics', title: '  ',  icon:' local_shipping    ', class: '' },
   { path: 'albumcategory', title: '  ',  icon:' emoji_symbols', class: '' },
   { path: 'stars', title: '    ',  icon:' redeem   ', class: '' },
   { path: 'home', title: '    ',  icon:' redeem   ', class: '' },
   { path: 'pharma', title: '    ',  icon:' redeem   ', class: '' },

  { path: 'album', title: '  ',  icon:' emoji_symbols', class: '' },
  { path: 'problem', title: '  ',  icon:' library_books', class: '' },
  { path: 'settings', title: '  ',  icon:' settings', class: '' },

];


@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  menuItems: any[];


  constructor( private router: Router) { }

  ngOnInit() {

     
    this.menuItems = ROUTES.filter(menuItem => menuItem);
  }
  logout(){
    localStorage.clear();
    this.router.navigate(['/login']);  }


  isMobileMenu() {
      if ($(window).width() > 991) {
          return false;
      }
      return true;
  };
}
